.navbar {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(90deg, #f57c00, #ffb74d);
  padding: 10px 20px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  position: sticky;
  top: 0;
  z-index: 100;
}

.nav-item {
  color: white;
  padding: 8px 15px;
  cursor: pointer;
  border-radius: 25px;
  margin: 5px;
  transition: 0.3s;
  font-weight: bold;
}

.nav-item:hover {
  background: rgba(255,255,255,0.2);
}

.nav-item.active {
  background: white;
  color: #f57c00;
  box-shadow: 0 0 5px rgba(0,0,0,0.2);
}
