.animal-card {
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0,0,0,0.1);
  overflow: hidden;
  transition: transform 0.3s, box-shadow 0.3s;
  cursor: pointer;
  display: flex;
  flex-direction: column;
}

.animal-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 30px rgba(0,0,0,0.2);
}

.animal-card img {
  width: 100%;
  height: 180px;
  object-fit: cover;
}

.animal-card h3 {
  margin: 10px;
  font-size: 1.2rem;
}

.animal-card p {
  margin: 0 10px 10px 10px;
  font-size: 0.9rem;
  color: #666;
}
